var code = "<h2 id=\"vueconfigjs-配置\">vue.config.js 配置</h2> <pre><code>module.exports = {\n  devServer: {\n    port: 8888,//端口号\n  },\n  lintOnSave: false,//关闭eslint检查\n  publicPath: &quot;./&quot;,//基本路径\n  assetsDir: &quot;static&quot;,//放置生成的静态资源 (js、css、img、fonts) 的目录\n  outputDir: &#39;docs&#39;,//输出文件目录\n  indexPath: &quot;index.html&quot;,//指定生成的 index.html 的输出路径 (相对于 outputDir)\n  filenameHashing: true,//文件名哈希\n  chainWebpack: config =&gt; {//是一个函数，允许对内部的 webpack 配置进行更细粒度的修改。\n    config.module\n      .rule(&#39;md&#39;)\n      .test(/\\.md$/)\n      .use(&#39;html-loader&#39;)\n      .loader(&#39;html-loader&#39;)\n      .end()\n      .use(&#39;markdown-loader&#39;)\n      .loader(&#39;markdown-loader&#39;)\n      .end()\n  }\n}</code></pre>";
